import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ComService } from '../core/com.service';
import * as Canlendar from '../../assets/js/calendar';

@Component({
  selector: 'app-server',
  templateUrl: './server.component.html',
  styleUrls: ['./server.component.css'],
})
export class ServerComponent implements OnInit {
  chatList: any = [];
  ws: any;
  rid: any = 0;
  // 
  user: any;
  // 
  userList = [];
  // id
  myID: number;
  rightShow: string = '';
  chatElem: any;
  stateMenuShow: boolean = false;
  allHistory: any = [];
  rightHistory: any[] = [];
  // 
  files: any = [];
  // /
  // 
  isShowUserList: boolean = false;
  // 
  historyDate: string = '';
  // 
  isShowMonthCalendar: boolean = false;
  historyLen :number = 0;
  constructor(private com: ComService, private route: ActivatedRoute) {}

  ngOnInit(): void {
    this.historyDate = this.com.formatTime('', 'YYYY-MM-DD');
    this.initCalendar();
    this.chatElem = document.getElementById('chat');
    this.com.getUserList((data) => {
      console.log('com.getUserList data: ', data);
      if (data && data.length) {
        // 
        let storage = window.localStorage.unread;
        try{
          storage = JSON.parse(storage) || {};
        }catch(e){
          storage = {}
        }
        data.forEach((d) => {
          // 5s
          storage[d.id] && (d.unreadOption = storage[d.id])
          d.state = Date.now() - d.note_at / 1000000 < 5000;
        });
        this.userList = data;
        this.changeUser(0);        
      }
    });
    // 
    let random = Date.now().toString();
    this.com.pollingStart(this, random, 'role=1');
  }
  clickImg(e) {
    if (e.target.tagName.toLowerCase() === 'img') {
      this.com.previewOption.isShowMask = true;
      this.com.previewOption.src = e.target.src;
    }
  }
  initCalendar() {
    let callback = (y, m, d) => {
      this.historyDate = this.com.formatTime(
        y + '-' + m + '-' + d,
        'YYYY-MM-DD'
      );
    };
    new Canlendar({
      el: '#schedule-box',
      clickCb: callback,
      nextMonthCb: callback,
      nextYeayCb: callback,
      prevMonthCb: callback,
      prevYearCb: callback,
    });
  }
  keepEnd: boolean = false;
  fileEmiter(e) {
    this.keepEnd = !this.keepEnd;
  }
  // 
  getSpecialHistory(date) {
    if (this.allHistory.length) {
      this.rightHistory = this.allHistory.filter((el) => {
        return el.timeFormat.indexOf(date) > -1;
      });
      this.isShowMonthCalendar = false;
    }
  }
  showUserList() {
    this.isShowUserList = !this.isShowUserList;
  }
  // 
  changeUser(i) {
    this.user = this.userList[i];   
    // 
   /* let unread = window.localStorage.unread;
    try{
      unread = JSON.parse(unread) || {};
      unread[this.user.id] = {unread:0,id:[],chatID:''}
      window.localStorage.unread = JSON.stringify(unread);
    }catch(e){
      unread = {}
    }*/
    
    this.getHistory();
  }
  getHistory() {
    this.com.getHistory(this, this.user.id, (data) => {
      // this.chatList = data || [];
      if (this.rightShow) {
        this.rightHistory = data;
        this.allHistory = data;
      }
    });
  }
  logout() {
    // 
    this.com.logout();
    this.com.isLogin = false;
  }

  catchTab(e, callback) {
    callback && callback();
    e.stopPropagation();
  }
  showStateMenu() {
    this.stateMenuShow = true;
  }
  hideShowing() {
    this.stateMenuShow = false;
    this.isShowUserList = false;
    this.isShowMonthCalendar = false;
  }
  imgSrc: string = '';

  rightHitoryShow(toggle) {
    if (!toggle) {
      this.com.getHistory(this, this.user.id, (data) => {
        // 
        this.rightShow = 'right-show';
        this.rightHistory = data;
        this.allHistory = data;
      });
    } else {
      this.closeHistory();
    }
  }
  closeHistory() {
    this.rightHistory = [];
    this.rightShow = '';
  }
  showMonthCalendar() {
    this.isShowMonthCalendar = !this.isShowMonthCalendar;
  }
  chooseDateConfirm() {
    this.getSpecialHistory(this.historyDate);
  }
}
