import {
  HttpClient,
  HttpErrorResponse,
  HttpEventType,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { throwError } from 'rxjs';
import { catchError, last } from 'rxjs/operators';
@Injectable({
  providedIn: 'root',
})
export class ComService {
  // 
  alert: { msg: string; show: boolean } = { msg: '', show: false };
  ws: any;
  data: any = {};
  previewOption: { isShowMask: boolean; src?: string } = {
    isShowMask: false,
  };

  api = {
    login: '/api/auth/login', //post
    register: '/api/auth/register', //post
    message: '/api/message/', //post
    polling: '/api/message/polling', //get
    history: '/api/message/record', //get
    userList: '/api/user/list', // get
    logout: '/api/auth/logout', //post
    userInfo: '/api/user/', //{uid},get
    files: '/file/', // 
  };
  who: any = null; // 
  isLogin: boolean = true;
  constructor(private http: HttpClient, private router: Router) {
    this.windowActive();
  }
  isWindowActive: boolean = true;
  // 
  formatTime(
    date: string | number | Date = '' /*  */,
    fmt = 'YYYY-MM-DD' /*YYYY-MM-DD hh:mm:ss */
  ) {
    //ios
    if (typeof date === 'string') {
      date = date.replace(/-/g, '/');
    }
    date = date ? new Date(date) : new Date();
    var o = {
      'Y+': date.getFullYear(), //
      'M+': date.getMonth() + 1, //
      'D+': date.getDate(), //
      'h+': date.getHours(), //
      'm+': date.getMinutes(), //
      's+': date.getSeconds(), //
      'q+': Math.floor((date.getMonth() + 3) / 3), //
      S: date.getMilliseconds(), //
    };
    if (/(Y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (o['Y+'] + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
  timeFromNow(time) {
    //
    const timestamp = Date.now();
    const createTime = new Date(time);
    time = createTime.getTime();
    const diff = timestamp - time;
    if (diff < 60 * 1000) {
      return '';
    } else if (diff < 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 1000)) + '';
    } else if (diff < 24 * 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 60 * 1000)) + '';
    } else {
      return this.formatTime(time, 'YYYY-MM-DD hh:mm');
    }
  }
  windowActive() {
    var hiddenProperty =
      'hidden' in document
        ? 'hidden'
        : 'webkitHidden' in document
        ? 'webkitHidden'
        : 'mozHidden' in document
        ? 'mozHidden'
        : null;
    var visibilityChangeEvent = hiddenProperty.replace(
      /hidden/i,
      'visibilitychange'
    );
    var onVisibilityChange = function () {
      if (document[hiddenProperty]) {
        // -
        this.isWindowActive = true;
      } else {
        // '
        this.isWindowActive = false;
      }
    };
    document.addEventListener(visibilityChangeEvent, onVisibilityChange);
    window.onblur = () => {
      
      this.isWindowActive = false;
    };
    window.onfocus = () => {
      
      // - 
      this.sendReadedMsg();
      this.isWindowActive = true;
    };
  }
  showAlert(msg) {
    this.alert.show = true;
    this.alert.msg = msg;
    setTimeout(() => {
      this.alert.show = false;
    }, 2000);
  }

  doHttpResponse(res, success, fail?) {
    let code = res.code;
    if (code) {
      if (code === 3 || code === 6) {
        // 
        this.isLogin = false;
        this.showAlert('');
        setTimeout(() => {
          this.router.navigate(['/auth/login']);
        }, 2000);
      } else {
        fail ? fail(res) : this.showAlert(res.msg);
      }
    } else {
      // 
      success && success(res.data);
    }
  }
  // 
  sendMsg(msg, success?, fail?) {
    this.http
      .post(this.api.message, msg)
      .pipe(
        catchError(
          this.handleError('sendMsg', () => {
            fail && fail();
          })
        )
      )
      .subscribe((res) => {
        this.doHttpResponse(
          res,
          (data) => {
            success && success(data);
          },
          fail
        );
      });
  }
  private handleError(uri: string, failCallback?) {
    return (error: HttpErrorResponse) => {
      if (failCallback) {
        setTimeout(() => {
          failCallback();
        }, 3000);
      } else {
        this.showAlert('http request ' + uri + ' error ');
      }

      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        console.error(
          'A client-side or network error occurred. Handle it accordingly.'
        );
        this.showAlert('');
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong.
        console.error(
          `Backend returned code ${error.status}, ` + `body was: ${error.error}`
        );
        this.showAlert('');
      }
      // Return an observable with a user-facing error message.
      return throwError('Something bad happened; please try again later.');
    };
  }
  // 
  pollingStart(obj, random: string, domain?: string) {
    !this.who && (this.who = obj);
    let options: any = {
      observe: 'response',
    };

    this.http
      .get(
        this.api.polling + '?random=' + random + (domain ? '&' + domain : ''),
        options
      )
      .pipe(
        catchError(
          this.handleError('polling', () => {
            this.pollingStart(obj, random, domain);
          })
        )
      )
      .subscribe((res: any) => {
        if (!obj.myID) {
          obj.myID = Number(res.headers.get('Uid'));
        }
        // 
        let isNewConnect = res.headers.get('Request-Times') === '1';
        let ret = res.body;
        let isServer = 'rid' in obj;
        this.doHttpResponse(ret, (data) => {
          if (isNewConnect) {
            if (!isServer) {
              // 
              data && data.length && this.doHistory(data, obj);
            }
          } else {
            // 
            data && data.length && this.doPolling(data, obj, isServer);
          }
        });
        // -
        this.isLogin && this.pollingStart(obj, random, domain);
      });
  }
  // 
  preview(file, callback) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (e) => {
      callback(e.target.result);
    };
  }
  // 
  uploadFile(fileObj, callback, progressCallback?) {
    const formData = new FormData();
    formData.append('file', fileObj.file);
    let api = 'https://io.imdraw.com:444';
    const req = new HttpRequest('POST', api, formData, {
      reportProgress: true,
    });
    this.http
      .request(req)
      .pipe(
        catchError(
          this.handleError('uploadFile', () => {
            callback(fileObj, true);
            this.showAlert('!');
          })
        )
      )
      .subscribe((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          fileObj.uploadProgress = Math.round(
            (100 * event.loaded) / event.total
          );
          progressCallback && progressCallback(fileObj);
        } else if (event instanceof HttpResponse) {
          let body: any = event.body;
          fileObj.src = api + '/' + body.key;
          fileObj.type === 'file' && (fileObj.src += '?operation=download');
          callback(fileObj);
        }
      });
  }

  /**
   * // fid =   
   * @param data
   * @param obj
   * @param isServer
   */
  doPolling(data, obj, isServer?) {
    
    data.forEach((d) => {
      let fid = d.fid;
      let isISend = fid === obj.myID;
      let type = d.type;
      switch (type) {
        // 
        case 0:
          this.doType0(isISend, obj, d, isServer);
          break;
        // 
        case 1:
          this.doType1(d, obj, isISend, isServer);
          break;
        // 
        case 3:
          this.doType3(isISend, d, isServer, obj);
          break;
      }
    });
  }
  // 
  doType1(d, obj, isISend, isServer) {
    // 
    if (!isISend) {
      if (isServer) {
        // 
        // --
        if (!isISend) {
          // 
          let isCuUserSend = obj.user.id === d.fid;
          if (isCuUserSend) {
            // 
            let i = obj.chatList.findIndex((x) => x.id === d.id);
            i > -1 && (obj.chatList[i].readed = true);
          }
        }
      } else {
        // 
        let i = obj.chatList.findIndex((x) => x.id === d.id);
        i > -1 && (obj.chatList[i].readed = true);
      }
    }
  }
  // 
  doType3(isISend, d, isServer, obj) {
    isISend &&
      this.showAlert(
        '' + d.fid + (d.data === 'online' ? '' : '') + ''
      );
    //  
    if (isServer) {
      let i = obj.userList.findIndex((x) => x.id === d.fid);
      i > -1 && (obj.userList[i].state = d.data === 'online');
    }
  }

  doType0(isISend, obj, d, isServer) {
    let fid = d.fid;
    let isCuUserSend = isServer ? obj.user.id === fid : !isISend;
    // 
    this.attachSPT(obj, d, isServer);
    // 
    if (this.isWindowActive) {
      // 
      if (!isISend) {
        // 
        if ((isServer && isCuUserSend) || !isServer) {
          
          // -
          let readedMsg: any = { id: d.id, type: 1 };
          isServer && (readedMsg.rid = obj.user.id);
          this.sendMsg(readedMsg);
          obj.chatList.push(d);
          this.jumpToChatBottom(obj);
        } else if (isServer && !isCuUserSend) {
          // +1;
          let userIndex = obj.userList.findIndex((u) => u.id === fid);
          let isInUserList = userIndex > -1;
          if (isInUserList) {
            let userInfo = obj.userList[userIndex];
            this.notifyAndUnreadPlus(userInfo, d);
          } else {
            this.getUserInfo(fid, (userInfo) => {
              this.notifyAndUnreadPlus(userInfo, d);
              obj.userList.unshift(userInfo);
            });
          }
        }
      } else {
        // -
        let i = obj.chatList.findIndex((x) => x.mark === d.mark);
        i === -1 ? obj.chatList.push(d) : Object.assign(obj.chatList[i], d);
      }
    } else {
      // 
      if (!isISend) {
        // +1
        obj.chatList.push(d);
        if ((isServer && isCuUserSend) || !isServer) {
          
          let userInfo = isServer ? obj.user : obj;
          this.notifyAndUnreadPlus(userInfo, d);
          this.jumpToChatBottom(obj);
        } else if (isServer && !isCuUserSend) {
          // +1;
          let userIndex = obj.userList.findIndex((u) => u.id === fid);
          let isInUserList = userIndex > -1;
          if (isInUserList) {
            let userInfo = obj.userList[userIndex];
            this.notifyAndUnreadPlus(userInfo, d);
          } else {
            this.getUserInfo(fid, (userInfo) => {
              this.notifyAndUnreadPlus(userInfo, d);
              obj.userList.unshift(userInfo);
            });
          }
        }
      } else {
        // -
        let i = obj.chatList.findIndex((x) => x.mark === d.mark);
        i === -1 ? obj.chatList.push(d) : Object.assign(obj.chatList[i], d);
      }
    }
  }

  notifyAndUnreadPlus(userInfo, d) {
    userInfo.state = true;
    let name = userInfo.name ? '' + userInfo.name : '' + d.fid;
    this.deskNotify(name, d.data);
    this.setUnreadPlus(userInfo, d);
  }
  setUnreadPlus(userInfo, msg) {
    userInfo.unreadOption = userInfo.unreadOption || {
      unread: 0,
      chatID: '',
      id: [],
    };
    userInfo.unreadOption.unread++;
    !userInfo.unreadOption.chatID &&
      (userInfo.unreadOption.chatID = 'chatList' + msg.id);
    userInfo.unreadOption.id.push(msg.id);
    this.updateUnreadStorage(userInfo.id, userInfo.unreadOption);
  }

  // id 0
  // 
  sendReadedMsg() {
    
    if (this.who) {
      
      let isServer = 'rid' in this.who;
      let unreadParentObj: any = isServer ? this.who.user : this.who;
      if (unreadParentObj) {
        
        let unreadOption = unreadParentObj.unreadOption;
        
        if (unreadOption && unreadOption.id && unreadOption.id.length) {
          let id = unreadOption.id[0];
          if (id) {
            
            this.inViewToSendReadMsg(
              unreadParentObj,
              this.who,
              'chatList' + id
            );
          }
        }
      } else {
        
        setTimeout(() => {
          this.sendReadedMsg();
        }, 1000);
      }
    } else {
      setTimeout(() => {
        this.sendReadedMsg();
      }, 1000);
    }
  }

  // 
  setMyInfoReaded(obj) {
    let clen = obj.chatList.length;
    
    for (let i = obj.historyLen; i < clen; i++) {
      let dd = obj.chatList[i];
      if (dd.fid === obj.myID) {
        // 
        dd.readed = true;
      }
    }
    obj.historyLen = clen;
  }
  updateUnreadStorage(id, unreadOption) {
    let storage: any = window.localStorage.unread;
    try {
      storage = (storage && JSON.parse(storage)) || {};
    } catch (e) {
      storage = {};
    }
    storage[id] = unreadOption;
    window.localStorage.unread = JSON.stringify(storage);
  }
  // 
  doHistory(data, obj) {
    let isServer = 'rid' in obj;
    // 
    this.attachSPTWidthArr(data, obj, isServer);
    obj.chatList = data;

    // unreadOption  
    // - --
    //  => --
    this.setReadInfo(data, obj, isServer);
  }
  // 
  setReadInfo(data, obj, isServer) {
    let len = (obj.historyLen = data.length);
    let unreadParentObj = isServer ? obj.user : obj;
    unreadParentObj.unreadOption = { unread: 0, id: [] };
    let rightMsgReaded = false;
    let rightStopIndex = -1;
    let leftMsgReaded = false;
    for (let i = len; i--; ) {
      let d = data[i];
      let isDbReaders = d.readers.split(',').length > 1;
      let isIsend = d.fid === obj.myID;
      let isSysMsg = d.fid === 0;
      // == 
      isServer && (isIsend = isSysMsg || isIsend);
      if (isIsend) {
        leftMsgReaded = true;
        if (isDbReaders) {
          // 
          rightMsgReaded = true;
          // 
          rightStopIndex = i;
          break;
        } else {
          // 
          d.readed = rightMsgReaded;
          // 
        }
      } else {
        // -
        rightMsgReaded = true;
        rightStopIndex = i;

        if (isDbReaders) {
          // 
          break;
        } else {
          // 
          if (!leftMsgReaded) {
            unreadParentObj.unreadOption.unread++;
            unreadParentObj.unreadOption.id.push(d.id);
          }
          !unreadParentObj.unreadOption.chatID &&
            (unreadParentObj.unreadOption.chatID = 'chatList' + d.id);
        }
      }
    }
    // 
    if (rightMsgReaded && rightStopIndex > -1) {
      for (let i = rightStopIndex + 1; i--; ) {
        data[i].readed = true;
      }
    }
    if(isServer){
      this.updateUnreadStorage(obj.user.id,unreadParentObj.unreadOption)
    }
    
    // 
    // this.jumpToUnread(obj);
    // 
    this.jumpToChatBottom(obj);
  }
  jumpToChatBottom(obj) {
    setTimeout(() => {
      obj.chatElem.scrollTop = obj.chatElem.scrollHeight;
    }, 1000);
  }
  // 
  jumpToUnread(obj) {
    let isServer = 'rid' in obj;
    let unreadParentObj = isServer ? obj.user : obj;
    if (unreadParentObj.unreadOption.unread) {
      setTimeout(() => {
        let a = document.getElementById('linkToChatID');
        a && a.click();
        setTimeout(() => {
          this.inViewToSendReadMsg(unreadParentObj, obj);
        }, 1500);
      }, 1000);
    }
  }
  inViewToSendReadMsg(unreadParentObj, obj, id?) {
    let nowAt = document.getElementById(
      id || unreadParentObj.unreadOption.chatID
    );
    if (nowAt) {
      let f = nowAt.offsetTop;
      let h = nowAt.offsetHeight;
      let h1 = obj.chatElem.clientHeight;
      let t = obj.chatElem.scrollTop;

      
      if (f > t && f + h < t + h1 && unreadParentObj.unreadOption.id.length) {
        // 
        unreadParentObj.unreadOption.id.forEach((el) => {
          let msg: any = { id: el, type: 1 };
          'rid' in obj && (msg.rid = obj.user.id);
          this.sendMsg(msg);
        });
        // 0
        let clearUnread = { unread: 0, id: [], chatID: '' };
        if ('rid' in obj) {
          obj.user.unreadOption = clearUnread;
          this.updateUnreadStorage(obj.user.id, clearUnread);
        } else {
          obj.unreadOption = clearUnread;
        }
      }
    }
  }
  // 
  getUserList(success) {
    this.http
      .get(this.api.userList)
      .pipe(catchError(this.handleError('getUserList')))
      .subscribe((res) => {
        this.doHttpResponse(res, success);
      });
  }
  getUserInfo(id, cb) {
    this.http
      .get(this.api.userInfo + id)
      .pipe(catchError(this.handleError('getUserInfo')))
      .subscribe((res: any) => {
        this.doHttpResponse(res, cb);
      });
  }
  login(data) {
    this.http
      .post(this.api.login, data)
      .pipe(catchError(this.handleError('login')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('');
          this.isLogin = true;
          // 
          setTimeout(() => {
            this.router.navigate(['/server']);
          }, 2000);
        });
      });
  }
  register(data) {
    this.http
      .post(this.api.register, data)
      .pipe(catchError(this.handleError('register')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('');
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        });
      });
  }

  logout() {
    this.http
      .post(this.api.logout, null)
      .pipe(catchError(this.handleError('logout')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('!');
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        });
      });
  }
  getHistory(obj, id, success) {
    let options: any = {
      observe: 'response',
    };
    this.http
      .get(this.api.history + (id ? '?id=' + id : ''), options)
      .pipe(catchError(this.handleError('getHistory')))
      .subscribe((res: any) => {
        if (!obj.myID) {
          obj.myID = Number(res.headers.get('Uid'));
        }
        let ret = res.body;
        this.doHttpResponse(ret, (data) => {
          this.doHistory(data, obj);
          success(data);
        });
      });
  }
  // 
  tmpTime: string = '';
  attachSPT(obj, d, isServer) {
    let isIsend = d.fid === obj.myID;
    let isSysMsg = d.fid === 0;
    d.isServer = isSysMsg ? true : isServer ? isIsend : !isIsend;
    d.position = isServer ? (isSysMsg ? false : !isIsend) : !isIsend;
    let fromNowTime = this.timeFromNow(d.time / 1000000);
    d.timeStr = this.tmpTime === fromNowTime ? '' : fromNowTime;
    this.tmpTime !== fromNowTime && (this.tmpTime = fromNowTime);
    d.timeFormat = this.formatTime(d.time / 1000000, 'YYYY-MM-DD hh:mm:ss');
  }
  attachSPTWidthArr(data, obj, isServer) {
    data.forEach((d) => {
      this.attachSPT(obj, d, isServer);
    });
  }

  notifyInstanceEvent(content, title) {
    var instance = new Notification(title, {
      body: content,
      icon: '/assets/images/desk-logo.png',
      tag: '1212212',
      // sound:'/assets/sounds/msg.wav',
      renotify: true,
    });
    instance.onclick = function (e) {
      window.focus();
      instance.close();
      //  
    };
    instance.onerror = function () {};
    instance.onshow = function () {
      try {
        var msg = new Audio();
        msg.src = '/assets/sounds/msg.wav';
        msg.play();
      } catch (e) {
        
      }
    };
    instance.onclose = function () {};
  }
  // 
  timer: any;
  deskNotify(title, msg) {
    var Notification = window.Notification;
    let div = document.createElement('div');
    div.innerHTML = msg;
    let content = '';
    Array.from(div.childNodes).forEach((el: any) => {
      let nodeType = el.nodeType;
      if (nodeType === 1) {
        // 
        let id = el.id;
        if (id.substring(0, 3) === 'img') {
          content += '[]';
        } else if (id.substring(0, 4) === 'file') {
          content += '[]' + el.innerText;
        } else {
          content += el.innerText;
        }
      } else if (nodeType === 3) {
        // 
        content += el.textContent;
      }
    });
    content = content.substring(0, 100) + (content.length > 100 ? '...' : '');

    if (Notification) {
      //
      if (Notification.permission == 'granted') {
        //

        this.notifyInstanceEvent(content, title);
      } else {
        //(Notification.requestPermission())
        Notification.requestPermission(function (status) {
          if (status === 'granted') {
            this.notifyInstanceEvent(content, title);
          } else {
            //
            return false;
          }
        });
      }
    } else {
      //(IE)
      var index = 0;
      clearInterval(this.timer);
      this.timer = setInterval(function () {
        let titleElem = document.getElementsByName('title')[0];
        if (index % 2) {
          titleElem.innerText = '' + title; //
        } else {
          titleElem.innerText = '' + title;
        }
        index++;

        if (index > 20) {
          clearInterval(this.timer);
        }
      }, 500);
    }
  }
}
