import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ComService } from 'src/app/core/com.service';

@Component({
  selector: 'send-area',
  templateUrl: './send-area.component.html',
  styleUrls: ['./send-area.component.css'],
})
export class SendAreaComponent implements OnInit {
  @Input() who: any;
  // @Output() sendListen = new EventEmitter();
  @Input() keepEnd: boolean = false;
  isServer: boolean = false;
  target = null;
  constructor(private com: ComService, private _sanitizer: DomSanitizer) {}
  hasHolder = true;
  editText: string = '';
  // angular
  fileCommentNode: any = null;
  ngOnInit(): void {
    this.target = document.getElementById('editDiv');

    this.isServer = 'rid' in this.who;
    this.setEditDivUsable();
  }
  ngOnChanges(changes: SimpleChanges): void {

    // 
    this.newFileNode(this.who.files[this.who.files.length - 1]);
    
  }

  newFileNode(file) {
    if (file) {
      let span: any = document.createElement('span');
      span.id = file.fid;
      span.contentEditable = false;
      span.className = 'd-inline-block';
      file.type === 'file' && (span.className += ' file-box');
      if (file.type === 'img') {
        let img: any = document.createElement('img');
        img.src = file.src;
        img.onerror = "this.src='/assets/images/error.jpg'";
        span.appendChild(img);
      } else {
        let icon = document.createElement('i');
        icon.className = 'iconfont icon-' + file.fileType;
        span.appendChild(icon);
        let link = document.createElement('a');
        link.href = file.src;
        link.textContent = file.name;
        link.className = 'file-name';
        span.appendChild(link);
      }
      this.target.appendChild(span);
      this.keepLastIndex(this.target);
    }
  }
  setBorder(d, e) {
    d.border = !d.border;
    let range = document.createRange();
    range.selectNodeContents(e.target); // document.getElementById('test')
    range.collapse(false);
    let sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
    this.editFocus();
  }
  // 
  editPaste(e) {
    var cbd = e.clipboardData;
    console.log('editPaste: ', cbd);
    var ua = window.navigator.userAgent;
    console.log('ua: ', ua);
    //  Safari  return
    if (!(e.clipboardData && e.clipboardData.items)) {
      console.log('e.clipboardData: ', e.clipboardData);
      return;
    }
    // MacChrome49 FinderBug Hack
    if (
      cbd.items &&
      cbd.items.length === 2 &&
      cbd.items[0].kind === 'string' &&
      cbd.items[1].kind === 'file' &&
      cbd.types &&
      cbd.types.length === 2 &&
      cbd.types[0] === 'text/plain' &&
      cbd.types[1] === 'Files' &&
      ua.match(/Macintosh/i) &&
      Number(ua.match(/Chrome\/(\d{2})/i)[1]) < 49
    ) {
      console.log('Mac Chorme49');
      return;
    }
    !cbd.items.length && this.com.showAlert('');
    for (var i = 0; i < cbd.items.length; i++) {
      var item = cbd.items[i];
      if (item.kind == 'file') {
        // blob  
        var blob = item.getAsFile();
        if (blob.size === 0) {
          return;
        }
      }
      //  -,blob
      if (item.type.indexOf('image') > -1) {
        e.preventDefault();
        this.com.preview(blob, (src) => {
          // imgdata
          let file = {
            type: 'img',
            src,
            file: blob,
            fid: 'img' + Date.now(),
          }
          this.who.files.push(file);
          this.newFileNode(file);
          this.keepLastIndex(this.target);
        });
      }
    }
  }
  // 
  keepLastIndex(obj) {
    if (window.getSelection) {
      //ie11 10 9 ff safari
      obj.focus(); //ff
      var range = window.getSelection(); //range
      range.selectAllChildren(obj);
      range.collapseToEnd();
    }
  }
  setEditDivUsable() {
    // 
    if (this.isServer) {
      this.who.userList.length &&
        this.target.setAttribute('contenteditable', true);
    } else {
      this.target.setAttribute('contenteditable', true);
    }
  }
  editFocus() {
    this.hasHolder = false;
  }
  editBlur(e) {
    this.editText = e.target.innerHTML;
    this.hasHolder = !this.editText;
  }

  editInput(e) {
    // console.log('editInput: ');
  }
  editKeyUp(e) {
    if (e.keyCode === 13) {
      // 
      this.send();
    }
  }
  ngAfterContentChecked(): void {
    this.setEditDivUsable();
  }
  clickImg(e) {
    this.com.previewOption.isShowMask = true;
    this.com.previewOption.src = e.target.src;
  }

  send() {
    let html = this.target.innerHTML;
    
    // -<br>
    let div: any = document.createElement('div');
    div.innerHTML = html;
    let nodes: any = Array.from(div.childNodes);
    for (let i = nodes.length; i--; ) {
      let n = nodes[i];
     
      if (n.nodeType === 8) {
        this.target.innerHTML = '';
        this.target.appendChild(n);
      }
    }
    let editTxt = div.innerHTML;
    editTxt = editTxt.replace(/<br>/g, '').trim();

    if (editTxt) {
      // editText
      let content: any = { data: editTxt, mark: Date.now().toString() };
      // 
      content.isServer = this.isServer;
      content.position = false;
      this.who.chatList.push(content);
      this.isServer && (content.rid = this.who.user.id);
      this.com.jumpToChatBottom(this.who);

      let count = 0;
      this.who.files.forEach((file, i) => {
        editTxt.indexOf(file.fid) === -1 && this.who.files.splice(i, 1);
      });
      console.log('this.who.files.length',this.who.files.length)
      if (this.who.files.length) {
        // files 
        this.who.files.forEach((el) => {
          this.com.uploadFile(
            el,
            (file, err?) => {
              if (err) {
                // 
                let i = this.who.chatList.findIndex(
                  (x) => x.mark === content.mark
                );
                i > -1 && (this.who.chatList[i].isUploaded = -1);
              } else {
                count++;
                let span = null;
                console.log(div.childNodes);
                Array.from(div.childNodes).forEach((el: any) => {
                  console.log('el.id: ', el.id);
                  console.log('file.fid', file.fid);
                  el.id === file.fid && (span = el);
                });
                try {
                  if (file.type === 'img') {
                    span.children[0].setAttribute('src', file.src);
                  } else if (file.type === 'file') {
                    span.children[1].setAttribute('href', file.src);
                    span.children[1].setAttribute('download', file.name);
                  }
                } catch (e) {}
                if (count === this.who.files.length) {
                  content.data = div.innerHTML;
                  this.sendMsg(content);
                }
              }
            },
            (file) => {
              // chatList 
              // content.uploadProgress = file.uploadProgress;
            }
          );
        });
      } else {
        // 
        this.sendMsg(content);
      }
    } else {
      this.com.showAlert('');
    }
    this.target.innerHTML = '';
  }
  sendMsg(content) {
    console.log('sendMsg: ', content);
    this.com.sendMsg(
      content,
      (data) => {
        console.info('');
        this.who.files = [];
        let i = this.who.chatList.findIndex((x) => x.mark === content.mark);
        i > -1 && (this.who.chatList[i].isUploaded = 1);
        setTimeout(() => {
          this.com.jumpToChatBottom(this.who);
        }, 1000);
      },
      (res) => {
        console.info('', res);
        // -
        if (res && res.msg === '') {
          let offline: any = {
            data: ',',
            mark: Date.now().toString(),
          };
          offline.isServer = true;
          offline.position = true;
          this.who.chatList.push(offline);
          setTimeout(() => {
            this.com.jumpToChatBottom(this.who);
          }, 1000);
        } else {
          let i = this.who.chatList.findIndex((x) => x.mark === content.mark);
          i > -1 && (this.who.chatList[i].isUploaded = -1);
          console.log('i', i);
          setTimeout(() => {
            this.com.jumpToChatBottom(this.who);
          }, 1000);
          this.com.showAlert(res.msg);
        }
      }
    );
  }
}
